* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main_div{
  width: 100%;
  height: 100vh;
  background: #badc58;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width: 25%;
  height: 60vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1{
  color: rgb(33, 32, 32);
  background: transparent;
  font-size: 50px;
  /* background-color: #ffffff; */
  padding: 6px 0 2px 0;
  margin: 100%;
  box-shadow: 5px 5px 5px 5px rgba(0,0,0,0.3);
}
