.btn_div{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
 

 
button{
  min-height: 40px;
  border: none;
  outline: none;
  margin-top: 30px;
  padding: 0 15px;
  border-radius: 5%;
  font-size: 20px;  
  letter-spacing: 2px;  
  cursor: pointer;
  background-color: #30336b;
  color: #fff;
  border-color:transparent;
  font-family: sans-serif;
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.3);

}


button:hover{
 background-color: #22a6b3;
}

